// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.REPORTLOSSMANAGEMENT_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_FRMLOSSORDERS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.REPORTLOSSMANAGEMENT_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.REPORTLOSSMANAGEMENT_FILE
    })
  })
}

// 获取储位精准信息
export const handleChuweiInfo = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REPORTLOSSMANAGEMENT_UPDATE, data: {createTableLoading: true}})
  let api = API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.REPORTLOSSMANAGEMENT_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.REPORTLOSSMANAGEMENT_FILE
    })
  })
}

// 执行-调成本中心
export const handleCarriedOut = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REPORTLOSSMANAGEMENT_UPDATE, data: {modalTuttonLoading: true, tableLoading: true}})
  let api = API_FRMLOSSORDERS_EXECUTE
  getData({api, ...reqData}).then(data => {
    console.log(data.code)
    // 相应的逻辑判断
    dispatch({
      type: actionType.REPORTLOSSMANAGEMENT_UPDATE,
      data: {
        isShowModal: false,
        codeNum: data.code
      }
    })
    callBack && callBack(data.code)
  }).catch(err => {
    dispatch({
      type: actionType.REPORTLOSSMANAGEMENT_FILE
    })
  })
}

