import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Row,
  Col
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reportLossManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '商品编号',
      width: 100,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 100,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 100,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '储位',
      width: 100,
      dataIndex: 'storeLocationName',
      key: 'storeLocationName'
    }, {
      title: '报损数量',
      width: 100,
      dataIndex: 'frmlossQty',
      key: 'frmlossQty'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'productPrice',
      key: 'productPrice'
    }, {
      title: '总价（元）',
      width: 100,
      dataIndex: 'frmlossAmount',
      key: 'frmlossAmount'
    }, {
      title: '备注',
      width: 100,
      dataIndex: 'remark',
      key: 'remark'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailRecord, match: {params}, history} = this.props
    handleGetDetailRecord({searchParams: {orderCode: params.id}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }

  render() {
    const {
      tableRecordLoading,
      searchResultRecord
    } = this.props
    let recordData = searchResultRecord.header ? searchResultRecord.header : ''
    console.log(recordData.shipperName)
    return (
      <div className='fullOrderDel'>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <div style={{paddingLeft: 20}}>
          <Row>
            <Col span={8}><h4>货主：{recordData.shipperName}</h4></Col>
            <Col span={8}><h4>报损类型：{recordData.frmlossTypeName}</h4></Col>
            <Col span={8}><h4>报损总额：{recordData.frmlossTotalAmount}</h4></Col>
            <Col span={24}><h4>备注：{recordData.remark}</h4></Col>
          </Row>
        </div>
        <div className='table-group' style={{margin: 0}}>
          <Table
            rowKey={record => record.id}
            loading={tableRecordLoading}
            columns={this.columns}
            scroll={{x: 840, y: 400}}
            dataSource={searchResultRecord.item}
            pagination={false}
          />
        </div>
      </div>
    )
  }
}



